@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

.card-root {
  margin: 10px;
  flex: 1 0 61%;
  flex-basis: 40%; }

.card-media {
  height: 200px; }

.card-header {
  display: flex;
  flex-direction: row;
  justify-content: space-between; }

.card-content {
  padding: 20px 0;
  display: flex;
  justify-content: flex-start; }

.reader-root {
  padding: 20px;
  width: 300px;
  height: auto; }

.reader-result {
  color: #000; }

.app-root {
  text-align: center;
  min-height: 100vh;
  background-color: #FFF; }

.header-container {
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  font-size: calc(10px + 2vmin);
  color: #2F2F2F;
  font-style: italic; }

.header {
  color: #000; }

.reader-container {
  display: flex;
  justify-content: center;
  align-items: center; }

.body-container {
  padding-right: 30px;
  padding-left: 30px;
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  font-family: 'Montserrat', sans-serif;
  font-size: calc(35px + 2vmin);
  font-weight: bold;
  color: #0000FF; }

.cards-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center; }

